
@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');
header{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 1;
    height: 70px;
    background: #E0E0E0;
    padding: 10px;
    box-shadow: 0 2px 5px rgba(0,0,0,.2);
}
header input {
    display: block;
    width: 480px;
    margin: 5px auto;
    height: 40px;
    border: none;
    padding: 0 16px;
    border-radius: 3px;
    font-size:18px;
    font-family: Josefin Sans, sans-serif;
}
header span {
    padding: 15px;
    color: #fff;
    position: absolute;
    right: 95px;
    top: 1px;
}
header img {
    width: 35px;
    height: 35px;
    border-radius: 20px;
    position: absolute;
    right: 60px;
    top: 8px;
}
header a {
    position: absolute;
    display: block;
    color: #fff;
    right: 15px;
    top: 10px;
    font-size: 25px;
    cursor: pointer;
    transition: color .2s;
}
header a:focus, header a:hover {
    color: #FF9800;
}
@media screen and (max-width: 1200px) {
header span{
      display: none;
}
}
@media screen and (max-width: 720px) {
header input{
      width: calc(100% - 64px);
      margin: 0 0 0 16px;
}
header span, header img {
      display: none;
}
}



