
.backdrop{
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(50,50,50,0.8);
}
form.edit-note{
  position: relative;
  width: 480px;
  max-width: 100%;
  margin: 25vh auto 0;
  background: #fff;
  padding: 15px;
  border-radius: 2px;
  box-shadow: 0 1px 50px #555;
}
form.edit-note input, form.edit-note textarea{
  width: 100%;
  max-width: 100%;
  border: none;
  padding: 4px;
  outline: none;
  font-size: 1.2em;
  color: #555555;
}
form.edit-note input{
  color:black;
  font-size: 1.4em;
}
form.edit-note button[type=submit]{
  font-size: 18px;
  float: right;
  background: #FF9800;
  color: #fff;
  border: none;
  border-radius: 3px;
  opacity: 1;
  cursor: pointer;
  padding: 4px 6px;
  margin: 0;
}
form.edit-note button{
  background: none;
  border: none;
  font-size: 20px;
  opacity: 0.6;
  cursor: pointer;
  transition: opacity .5s;
  margin: 0 4px 0 0;
}
form.edit-note button:hover, form.edit-note button:focus{
  opacity: 1;
}

/* modal transition */
.modal-transition{
  transition: opacity 0.3s ease;
  opacity: 1;
}
.modal-transition form{
  transition: transform 0.3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.modal-enter, .modal-leave {
  opacity: 0;
}
.modal-enter form,
.modal-leave form {
  transform: scale(1.5);
}



